<template>
  <section class="faq-aml">
    <div class="faq-aml__header">
      <h1
        @click="changeActiveComponent('faq')"
        :class="{active: activeTabComponent === 'faq'}"
        class="faq-aml__header__title">{{ t('faqAml.faq') }}</h1>
      <h1
        @click="changeActiveComponent('aml')"
        :class="{active: activeTabComponent === 'aml'}"
        class="faq-aml__header__title">{{ t('faqAml.aml') }}</h1>
    </div>
    <keep-alive>
      <component :is="activeTabComponent"></component>
    </keep-alive>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { defineAsyncComponent, ref } from 'vue'

export default {
  name: 'FaqAndAml',
  components: {
    faq: defineAsyncComponent(() => import('@/client/views/FaqAndAml/Faq')),
    Aml: defineAsyncComponent(() => import('@/client/views/FaqAndAml/Aml'))
  },
  setup () {
    const { t } = useI18n()
    const activeTabComponent = ref('faq')
    const changeActiveComponent = component => {
      activeTabComponent.value = component
    }
    return {
      t,
      activeTabComponent,
      changeActiveComponent
    }
  }
}
</script>

